@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}

body {
  background-color: #FFF;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Lato";
  color: #444;
}

html,
body {
  width: 320px;
  margin: 0;
  padding: 0;
  font-style: normal;
  box-sizing: border-box;
}

.main-header {
  position: relative;
  max-width: 320px;
  padding: 10px 20px 17px;
  background-color: #FFFFFF;
}

.main-header__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-text__image {
  min-width: 101px;
  min-height: 18px;
  align-items: center;
}

.main-nav {
  margin: 0;
  padding: 0;
  background-color: #FFFFFF;
}

.main-nav__toggle {
  content: "";
  display: inline-block;
  background-image: url("/source/img/menu_mobile.svg");
  background-repeat: no-repeat;
  width: 24px;
  height: 16px;
}

.main-nav__toggle--open .site-list {
  display: block;
}

.main-nav__wrapper {
  margin: 0;
  padding: 0;
  z-index: 1;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-family: "Oswald";
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
}

.main-nav__list--opened {
  display: block;
}

.main-nav__list--closed {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.site-list__link {
  text-decoration: none;
  color: #000;
  background-color: #FFFFFF;
}

.site-list__item {
  margin: 0;
  padding: 0;
  margin-bottom: 46px;
}
.site-list__item:last-child {
  margin-bottom: 24px;
}

.main-container {
  width: 320px;
  flex-grow: 1;
}

.promo {
